#hero {
	position: relative;
	height: 500px;
	padding-top: 50px;
	background-image: url("granola.jpg");
	background-repeat: no-repeat;
	background-size: 100%;
}

#hero .overlay {
	background-color: rgba(78, 78, 78, 0.7);
	position: absolute;
	top: 0;
	left: 0;
	width: 100%;
	height: 100%;
}

#fb-feed {
	padding: 40px 0;
	background-color: rgb(235, 237, 240);
}

#footer {
	height: 80px;
	line-height: 80px;
}
